.HeaderNav {
  margin: 0;
  padding: 0;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100%;
  -ms-flex: 1 1;
      flex: 1 1;
}
.FooterNav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  padding: 0;
}
@media (min-width: 500px) {
  .HeaderNav {
    -ms-flex-flow: row;
        flex-flow: row;
  }
}
@media (max-width: 600px) {
