.CarPoolNav {
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid rgba(0, 188, 212, 0.3);
  margin: 0;
  padding: 0;
  height: 100%;
  position: relative;
  font-size: 10px;
  text-transform: uppercase;
}
.CarPoolNav li {
  padding: 13px 15px 10px;
  background-color: transparent;
  color: #64625a;
  text-transform: uppercase;
  letter-spacing: .2em;
  -webkit-transition: color 200ms cubic-bezier(0.19, 1, 0.22, 1);
  -o-transition: color 200ms cubic-bezier(0.19, 1, 0.22, 1);
  transition: color 200ms cubic-bezier(0.19, 1, 0.22, 1);
}
.CarPoolNav li a {
  text-decoration: none;
  color: inherit;
}
.CarPoolNav li a:after {
  content: '';
  display: block;
  width: 0%;
  height: 1px;
  line-height: 1px;
  margin-top: 2px;
  background-color: #b6edeb;
  -webkit-transition: width 200ms cubic-bezier(0.19, 1, 0.22, 1);
  -o-transition: width 200ms cubic-bezier(0.19, 1, 0.22, 1);
  transition: width 200ms cubic-bezier(0.19, 1, 0.22, 1);
}
.CarPoolNav li:hover a {
  color: #87f1ee;
}
.CarPoolNav li:hover a:after {
  width: calc(100% - .2em);
}


