.Root {
  max-width: 400px;
  width: 100%;
}
.CardHeading {
  font-size: 20px;
  color: rgba(0, 0, 0, 0.87);
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 2px;
  line-height: 1;
}
.Social {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.ChartPaper {
  margin: -20px 15px 0px;
  border-radius: 2px;
  background: -webkit-linear-gradient(30deg, rgb(255, 167, 38), rgb(251, 140, 0));
  background: -o-linear-gradient(30deg, rgb(255, 167, 38), rgb(251, 140, 0));
  background: linear-gradient(60deg, rgb(255, 167, 38), rgb(251, 140, 0));
  -webkit-box-shadow: 0 12px 20px -10px rgba(255, 152, 0, 0.28),
    0 4px 20px 0px rgba(0, 0, 0, 0.12), 0 7px 8px -5px rgba(255, 152, 0, 0.2);
          box-shadow: 0 12px 20px -10px rgba(255, 152, 0, 0.28),
    0 4px 20px 0px rgba(0, 0, 0, 0.12), 0 7px 8px -5px rgba(255, 152, 0, 0.2);
}



